#shop-settings {
  & .dropdown-menu {
    width: max-content;
    max-width: 300px;

    @media (max-width: 991px) {
      border: none;
      max-width: 100%;
      width: 100%;
      position: static !important;
      transform: none !important;
      opacity: 1;
      visibility: visible;
      display: block !important;
    }
  }

  .a-simple-menu__link {
    @media (max-width: 991px) {
      display: none;
    }
  }
}
